body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Reset */
* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

/* Header */
header {
  background-image: linear-gradient(to right, rgb(80, 209, 209), rgb(101, 243, 248));
  margin: 0;
}

h1 { margin: 0; }

/* Nav */
nav {
  margin: 0;
}

nav > ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

nav li {
  margin: 0;
  padding: 0;
  text-align: center;
}

nav a {
  text-decoration: none;
}

.link-item {
  padding: 5px 0 3px;
  color: white;
}

/* Footer */
footer {
  text-align: center;
}

/* Users list */
.loader{
  text-align: center;
  padding: 50px 0;
}

.users-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.user-info {
  padding: 15px;
}

.user-info:hover {
  background-color: rgb(219, 255, 255);
  cursor: pointer;
  box-shadow: 
    4px 4px 2px whitesmoke;
}

.user-info h3 {
  margin: 5px 0;
}

/* Users list search parameters */
select {
  outline: none;
  border: none;
  background-color: rgb(80, 209, 209);
  color: white;
  padding: 2px;
  cursor: pointer;
}

input[type=text], input[type=number] {
  background-color: rgb(219, 255, 255);
  border: none;
  padding: 3px;
  outline: none;
  margin-right: 5px;
  margin-bottom: 5px;
}

::-webkit-input-placeholder {
  color: rgb(75, 201, 201);
}

::-ms-input-placeholder {
  color: rgb(75, 201, 201);
}

::placeholder {
  color: rgb(75, 201, 201);
}

/* Modal */
.modal {
  position: fixed;
  height: 400px;
  width: 320px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: ivory;
  border: 1px solid black;
  z-index: 2;
}

.modal-close {
  cursor: pointer;
  width: 25px;
  height: 25px;
  color: black;
  background-color: white;
  text-align: center;
  font-size: 20px;
  line-height: 25px;
  position: relative;
  top: -30px;
  float: right;
}

.mask {
  background: black;
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed;
  z-index: 1;
  cursor: pointer;
}

/* Responsive */
@media (min-width: 576px) {
  .users-list {
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  .user-info {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
  }
}

@media (min-width: 768px) {
  /* Header and Nav */
  nav ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  nav li {
    width: 200px;
    border-right: 1px solid white;
  }

  nav li:last-of-type {
    border-right: none;
  }

  .link-item {
    background-color: transparent !important;
  }

  /* Users list */
  .user-info {
    -webkit-flex-basis: 33%;
            flex-basis: 33%;
  }
}

@media (min-width: 992px) {
  .user-info {
    -webkit-flex-basis: 25%;
            flex-basis: 25%;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }

  .user-info {
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
  }
}

